.spanBlock {
    display: flex;
    gap: 10px;
    align-items: center;
}

.span {
    font-weight: 500;
    color: #0080FF;

    margin-top: 32px;
    margin-bottom: 24px;
}

.input {
    height: 36px;
    width: 100%;
    margin-top: 24px;
    margin-bottom: 16px;
    padding: 8px 0 8px 24px;

    font-family: Montserrat, sans-serif;
    font-size: 16px;
    font-weight: 500;
}

.input:focus {
    outline: none;
    border: 1px solid #D1D1D1;
}

