[data-theme='1'] {
    --color: #000000;
    --background: #FFFFFF;
}

[data-theme='2'] {
    --color: #000000;
    --background: #DDEEFF;
}

[data-theme='3'] {
    --color: #FFFFFF;
    --background: #212A34;
}

html {
    background-color: var(--background);
    color: var(--color);
}

.select {
    margin-top: 56px;
    min-width: 170px;
    height: 36px;
    border: 1px solid var(--color);
    background-color: var(--background);
    color: var(--color);
    padding-left: 10px;
}

.select option {
    background-color: var(--background);
}