.radio {
    appearance: none;
    margin-right: 10px;

    border-radius: 50%;
    width: 20px;
    height: 20px;
    border: 2px solid #000000;

    position: relative;
    top: 4px;
}

.radio::before {
    position: absolute;
    content: '';
    width: 10px;
    height: 10px;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    border-radius: 50%;
    background-color: #000000;
    display: none;
}


.radio:checked::before {
    display: block;
}

.label {
}

.options {
    display: flex;
    flex-direction: column;
    gap: 20px;
}
