.item {
    height: 48px;
    border-bottom: 1px solid #E5E5E5;
}

.item:last-of-type {
    border: none;
}

.users {
    table-layout: fixed;
    width: 100%;
    border-collapse: collapse;

    font-family: Roboto, sans-serif;
    font-size: 14px;
}

.buttonsContainer {
    align-self: flex-end;
    margin-bottom: 12px;
    display: flex;
    gap: 12px;
}

.container {
    max-width: 606px;
    margin-top: 32px;
    display: flex;
    flex-direction: column;
}

.thead {
    background-color: #E5E5E5;
    height: 36px;

    font-weight: 600;
}

.nameCol {
    padding-left: 16px;
}

.ageCol {
    padding-left: 16px;
}